.simpleTable {
    border-collapse: collapse;
    width: 100%;
    
    th, td {
        border: 1px solid #ddd;
        padding: 8px;
        text-align: left;
    }
    
    th {
        background-color: #f2f2f2;
        font-weight: bold;
    }
    
    tr:nth-child(even) {
        background-color: #f9f9f9;
    }

    .rowHeader {
        font-weight: bold;
    }

    .dataCell {
        text-align: right;
    }
}